import React, { PureComponent } from "react";

export default class BottomTicker extends PureComponent {
  render() {
    const { wall, UgcSettings } = this.props;
    const { title, tickerType, ticker_height, ticker_speed, title_font_size, subTitle_font_size, title_css_font, status, subTitle_backgroud_color, title_font_color, title_font_varient, ticker_position,subtitle_css_font, subTitle_font_color, subTitle, title_backgroud_color, subtitle_font_varient } = wall.CustomTicker


    const ugcStyle =  UgcSettings &&  UgcSettings.onsite_status &&  UgcSettings.onsite_overlay_poisition == "2" && UgcSettings.onsite_button_status
        ? { marginBottom: `${0}px` }  : {};

    return (
      <div
        id="wtickerBottom"
        className="headerTicker tickerBottom"
        style={{
          display: status === 1 &&
            ticker_position === "2"
              ? ""
              : "none",
          ...ugcStyle,
        }}
      >
        <div
          className={`tickerTitleView ${
            tickerType === 3 ? "ticker3titleView" : ""
          }`}
          style={{
            display: tickerType == 2 ? "none" : "flex",
            backgroundColor: title_backgroud_color,
            height:
              tickerType == 1
                ? ticker_height
                : ticker_height - 10 < 0
                ? ticker_height
                : ticker_height - 10,
          }}
        >
          <h1
            className={`setFont  sGFfonte-${title_font_varient}`}
            style={{
              fontFamily: title_css_font,
              fontWeight: 500,
              fontSize: ticker_height < 40 && title_font_size > 24
                  ? 20
                  : title_font_size,
              color: title_font_color,
            }}
          >
            {title}
          </h1>
        </div>
        <div
          className="tickerSubtitleView"
          style={{
            backgroundColor: subTitle_backgroud_color,
            height: ticker_height,
            display: status === 1 ? "" : "none",
          }}
        >
          <marquee
            scrollamount={
              ticker_speed === 5
                ? 5
                : ticker_speed === 10
                ? 10
                : 20
            }
          >
            <h2
              className={`setFont  sGFfonte-${subtitle_font_varient}`}
              style={{
                fontFamily: subtitle_css_font,
                display: "inline-block",
                fontSize: ticker_height < 40 &&  subTitle_font_size > 24 ? 20 : subTitle_font_size,
                color: subTitle_font_color,
              }}
            >
              {subTitle}
            </h2>
          </marquee>
        </div>
      </div>
    );
  }
}
